.data-view {
  height: 220px;
  display: grid;
  grid-template-rows: 26px auto 26px;
}

.data-view__textarea {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  width: 100%;
  height: 100%;
  padding: 4px;
  border: 2px solid #05c;
  border-radius: 5px;
  background: #fff;
  -webkit-box-shadow: 0 0 2px #000 inset;
          box-shadow: 0 0 2px #000 inset;
  resize: none;
  font-family: monospace;
}

.data-view__textarea--error {
  border-color: #c00;
}



